<template>
    <div>
        <van-nav-bar
        
        left-text="返回"
        
        @click-left='toMenu'
        @click-right="toregister({name:'Register'})"
        />
        <div class="logo">
            <img class="auto-img" src="../assets/images/loginImg.jpg" alt="">
        </div>
        <div class="sub">
            <div class="sub-login">
                <van-form @submit="login">
                    <van-field
                        type="text"
                        placeholder="请输入11位手机号"
                        left-icon="manager-o"                  
                        v-model="massge.value1"
                        name="pattern"
                    >
                    </van-field>
                    <van-field
                        :type="isEye ? 'password':'text'"
                        placeholder="以字母开头(请输入6-16位字符)"
                        left-icon="lock"
                        :right-icon="isEye ? 'closed-eye' : 'eye-o'"
                        v-model="massge.value3"
                        name="pswcl"
                        @click-right-icon='toggle()'
                    />
                    <div class="register-btn">
                        <van-button round block type="info" native-type="submit">立即登录</van-button>
                    </div>
                    <div class="register-btn">
                        <van-button round block type="info" @click="toregister({name:'Register'})">立即注册</van-button>
                    </div>
                    
                </van-form>
            </div>
        </div>
        
        
        <div class="forget fr" @click="toForget">忘记密码?</div>
    </div>
</template>

<script>
import { Toast } from 'vant';
import formCl from '../assets/js/formCl'
    export default {
        data() {
            return {
                massge:{
                    value1: '',
                    value3: '',
                } ,
                isEye:true,
                
            };
        },
        methods: {
            toMenu(){
                this.$router.push({name:'Menu'})
            },
            toForget(){
                this.$router.push({name:'Forget'})
            },
            toggle(){
                this.isEye = !this.isEye
            },
            toregister(e){
                this.$router.push(e)
            },
            login(){
                if(formCl.Verify(this.massge,this)){
                    //复制对象
                    let data = {
                        password:this.massge.value3,
                        phone:this.massge.value1
                    }

                    //请求密钥
                    data.appkey = this.appkey;

                    //开启加载提示
                    this.$toast.loading({
                        message: '加载中...',
                        forbidClick: true,

                        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
                        duration:0
                    });

                    //发起注册请求
                    this.axios({
                        method: 'POST',
                        url: '/login',

                        //post传递参数需要写在data
                        data
                    }).then(result => {
                        //关闭加载提示
                        this.$toast.clear();
                        
                        if(result.data.code == 200){
                            //跳转菜单页面
                            this.$router.push({name:"Menu"})
                            
                            
                            localStorage.setItem('login_Tk',result.data.token);
                        }else{
                            this.$toast(result.data.msg);
                        }
                    }).catch(err => {
                        //关闭加载提示
                        this.$toast.clear();
                        
                    })
                }
            }
        },
    }
</script>

<style lang="less" scoped>
.forget{
    margin-top: -90px;
    margin-right: 10px;
}
.logo{
    width: 100%;
    
    margin:-50px  auto;
    img{
        width: 100%;
        height: 100%;
    }
}

.sub-login{
    width: 80%;
    margin: 100px auto;
}

/deep/.van-nav-bar{
    background-color:transparent;
    
}
/deep/.van-field__control{
    text-indent: 1.5em;
}
/deep/.van-nav-bar__text{
    color: #f4b973;
}
/deep/ .van-button--info {
    margin-top: 30px;
    color: #fff;
    background-color: #f4b973;
    border: 1px solid #f4b973;
}
.forget{
    color: skyblue;
}
    
</style>